/**
 * Layout component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React, { useState } from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql } from "gatsby"

import Header from "./header"
import NavButtons from "./navButtons"
import "./layout.module.css"

import DialogContext from '../../contextAPI/dialog-context'
import {Dialog} from 'primereact/dialog';
import WordDataTable from '../WordDataTable/WordDataTable'

const Layout = ({ children, nextURL, prevURL }) => {

  // This controls the popup for the word information
  const [ dialog, updateDialog ] = useState({open: false, content: {}})
  const newDialog = (obj) => {
    updateDialog(obj)
  }
  
  return (
  <StaticQuery
    query={graphql`
      query SiteTitleQuery {
        site {
          siteMetadata {
            title
          }
        }
      }
    `}
    render={data => (
      <>
      <DialogContext.Provider value={{dialog: dialog, newDialog: newDialog}}>
        <Header siteTitle={data.site.siteMetadata.title} />
        <div
          style={{
            margin: `0 auto`,
            maxWidth: 960,
            padding: `0px 1.0875rem 1.45rem`,
            paddingTop: 0,
            marginTop: '50px'
          }}
        >
          <main>{children}</main>
          {(dialog.open === true) ? (
                <Dialog header={`WORD INFO : ${dialog.content.StrongNumber}`} visible={dialog.open} style={{width: '75vw', height: '70vw'}} contentStyle={{height: '200px', overflowY: 'auto'}}baseZIndex={300} modal={true} onHide={() => updateDialog({open: false, content: {}})}>
                    <WordDataTable rowData={Object.entries(dialog.content)} />
                </Dialog> 
            ) : null}
        </div>
        <NavButtons next={nextURL} prev={prevURL}/>
        </DialogContext.Provider>
      </>
    )}
  />
)}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
