import React from 'react'
import styles from './navButtons.module.css'
import { Link } from "gatsby"

const NavButtons = (props) => {
    const classNamesLeft = [styles.button, styles.left].join(' ')
    const classNamesRight = [styles.button, styles.right].join(' ')
    return (
        <React.Fragment>
            <Link to={props.prev} className={classNamesLeft}><i className="pi pi-chevron-left" style={{fontSize: '0.5em'}}></i></Link>
            <Link to={props.next} className={classNamesRight}><i className="pi pi-chevron-right" style={{fontSize: '0.5em'}}></i></Link>
        </React.Fragment>
    )
}

export default NavButtons;