import React from 'react'
import styles from './WordDataTable.module.css'

const Table = (props) => (<table className={styles.dialogTable}>
            {props.rowData.map(row => (<tr className={styles.row}><td className={styles.leftCell}>{row[0]}</td><td className={styles.rightCell}>{(typeof row[1] === "object")?`${row[1].book_id} ${row[1].chapter_id}:${row[1].verse_id}`:row[1]}</td></tr>))}
        </table>)

export default Table;